<template>
    <div id="Header" >
        <nav>
            <section class="logo"><i class="fab fa-html5"></i></section>
            <Slide v-if="window.width < 768" right width="200">
                 <ul>
                    <li><a href="#About" v-smooth-scroll='{duration: 1000}'>About Us</a></li>
                    <li><a href="#Features" v-smooth-scroll='{duration: 1000}'>Features</a></li>
                    <li><a href="#Pricing" v-smooth-scroll='{duration: 1000}'>Pricing</a></li>
                    <li><a href="#Contact" v-smooth-scroll='{duration: 1000}'>Contact</a></li>
                    <li><a href="#Download" v-smooth-scroll='{duration: 1000}'>Download</a></li>
                </ul>
            </Slide>
            <ul v-if="window.width >= 768">
                <li><a href="#About" v-smooth-scroll='{duration: 1000}'>About Us</a></li>
                <li><a href="#Features" v-smooth-scroll='{duration: 1000}'>Features</a></li>
                <li><a href="#Blog" v-smooth-scroll='{duration: 1000}'>Blog</a></li>
                <li><a href="#Contact" v-smooth-scroll='{duration: 1000}'>Contact</a></li>
                <li><a href="#Download" v-smooth-scroll='{duration: 1000}'>Download</a></li>
            </ul>
        </nav>
        <section class="slider">
            <article class="text">
                <h1>Awesome app<br>for your daily use.</h1>
                <p>A perfect landing page to showcase your new App.</p>
                <div class="groupBtn">
                    <button>click</button>
                    <button>click</button>
                </div>
            </article>
            <article class="img">
                <img class="back" src="../assets/img/iPhoneLeft.png" alt="iPhoneLeft">
                <img src="../assets/img/screenLeft.png" alt="iPhoneLeft">
                <img src="../assets/img/shadow.png" alt="shadow" class="shadow">
            </article>
        </section>
       <a href="#Header" v-smooth-scroll='{duration: 1000}' v-if="window.height >= 600" class="arrow"><i class="fas fa-arrow-up"></i></a>
    </div>
</template>

<script>
import { Slide } from 'vue-burger-menu'

export default {
    components:{
        Slide,
    },
    data(){
        return{
            window:{
                width:0,
                height:0,
            }
        }
    },
    created() {
    window.addEventListener('resize', this.handleResize)
    window.addEventListener('scroll', this.onScroll)
    this.handleResize();
    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize)
    },
    methods: {
        handleResize() {
        this.window.width = window.innerWidth;
       
        },
       onScroll(){
             this.window.height = window.scrollY ;
        },
    },
    

}
</script>
<style lang="scss" scoped>
@media(min-width:320px){
    #Header{
    height: auto;
    display: flex;
    flex-direction: column;
    background-image: url('../assets/img/Vector2.png');
    background-repeat: no-repeat;
    padding:70px 5% 150px;
    .arrow{
        position: fixed;
        bottom: 50px;
        right: 50px;
        width: 40px;
        height: 40px;
        background: #33198b;
        border-radius: 50%;
        color: white;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .slider{
        display:flex;
        padding-top: 1em;
        height: 375px;
        article.text {
            color: white;
            text-align: left;
            h1{font-size: 2em;}
            p{
                color:#aaa;
                margin: 8px 0;
            }
        }
        article.img{
            position: relative;
            left: -30%;
            bottom: -18px;
            width: 45%;
            margin: 0 auto;
        }
        .groupBtn{
            button{
                color:#d6d6d6;
                background-color: black;
                border: none;
                border-radius: 5px;
                font-size: 15px;
                padding: 5px 10px;
                &:hover{box-shadow: 0 0 4px 1px black;}
                &:first-child{
                    margin-right: 5px;
                }
            }
        }
        img{
            position: absolute;
            bottom: -90px;
            right: 51%;
            transform: translate(50%,0);
            height: 234px;
            &.back{
                right: 50%;
                bottom: -106px;
                height: 258px;
            }
            &.shadow{
                width: 209px;
                bottom: -146px;
                right: 51%;
                height: auto;
            }
        }
    }
    nav{
        display: flex;
        justify-content: space-around;
        font-size: 12px;
        .logo{
            display: none;
        i{
            font-size: 40px;
            color:#c93e07;
        }
        }
        ul{
            display: flex;
            flex-direction: column;
            list-style: none;
            li{
                padding: 10px;
                a{
                    color:white;
                    text-decoration: none;
                }
            }
        }
    }
}

}
@media(min-width:768px){
    #Header{
        max-width: 1440px;
        margin: 0 auto;
        background-position-x: center;
        padding: 20px 5% 246px;
        nav{
            justify-content: space-between;
            .logo{
            display: block;
            }
            ul{
                flex-direction: row;
                z-index: 2;
                font-size: 23px;
            }
        }
        .slider{
            article.img{
                left: 0;
                bottom: 100px;
                transform: scale(1.3);
            }
            article.text{
                padding-top: 110px;
            }
        }
    }
}


</style>

